import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Button,
  Input,
  message
} from 'antd'
import {connect} from 'react-redux'
import {Import} from 'HOC'
import {CsxSelect} from 'Form'
import {API_GETREQUISITIONSTTYPEKEYVALUE, API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE, API_REQUISITIONSORDERS_IMPORT} from 'api'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reqManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
  }

  componentDidMount() {

  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowFullModal: false, importModal: {}})
  }

  onInputChange(values) {
    const {importModal, update} = this.props
    update({importModal: {...importModal, ...values}})
  }

  handleBeforeImport = () => {
    const {update, importModal} = this.props
    let reqObj = {}
    reqObj = {...importModal}
    // if (!reqObj.costCenterCode) {
    //   message.error('请选择领用类型')
    //   return
    // }
    // if (!reqObj.costCenterCode) {
    //   message.error('请选择成本中心')
    //   return
    // }
    if (Object.prototype.toString.call(reqObj) === '[object Object]') {
      reqObj.requisitionsTypeName = reqObj.requisitionsTypeCode && reqObj.requisitionsTypeCode.children
      reqObj.requisitionsTypeCode = reqObj.requisitionsTypeCode && reqObj.requisitionsTypeCode.value
      reqObj.costCenterCode = reqObj.costCenterName && reqObj.costCenterName.value
      reqObj.costCenterName = reqObj.costCenterName && reqObj.costCenterName.children
    }
    update({tableLoading: true, importData: {...reqObj}})
  }
  /* 导入成功 */
  handleImportSuccess = res => {
    const {update, handleGetList, searchParams, page, size} = this.props
    update({tableLoading: false, isShowFullModal: false, importModal: {}})
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }

  // 我知道了
  handleExprotErrorCancel = () => {
    const {update} = this.props
    // update({isShowFullModal: false})
  }

  render() {
    const {
      importModal: {
        costCenterName,
        requisitionsTypeCode,
        remark
      },
      update,
      importModal,
      importData
    } = this.props
    console.log(importModal.costCenterName)
    return (
      <Modal
        title="创建导入"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1000}
      >
        <div className='content'>
          <Row type="flex" justify="start">
            <Col span={12}>
              <CsxSelect
                dateApi={API_GETREQUISITIONSTTYPEKEYVALUE}
                searchParams={{wmsOrderType: '库内领用'}}
                label='领用类型'
                require={true}
                selectItem={{data: requisitionsTypeCode, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  importModal.costCenterName = {}
                  update({...importModal})
                  this.onInputChange({requisitionsTypeCode: value})
                }}
              />
            </Col>
            <Col span={12}>
              <div className='commonInput'>
                <label>备注：</label>
                <div className='box'>
                  <Input value={remark} onChange={(e) => {
                    this.onInputChange({remark: e.target.value})
                  }} />
                </div>
              </div>
            </Col>
            <Col span={12}>
              <CsxSelect
                dateApi={API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE}
                label='成本中心'
                require={true}
                antd={{disabled: importModal.requisitionsTypeCode && importModal.requisitionsTypeCode.value ? false : true}}
                searchParams={{requisitionsTypeCode: importModal.requisitionsTypeCode && importModal.requisitionsTypeCode.value}}
                selectItem={{data: costCenterName, key: 'key', valueKey: ['key', 'value']}}
                onChangeSelect={(value) => {
                  this.onInputChange({costCenterName: value})
                }}
              />
            </Col>
          </Row>
          <Row type="flex" justify="end" style={{marginTop: '15px'}}>
            <Col>
              <Import
                action={{api: API_REQUISITIONSORDERS_IMPORT, data: {...importData}}}
                onOk={this.handleImportSuccess}
                onExprotError={this.handleExprotError}
                beforeImport={
                  this.handleBeforeImport
                }
                onExprotErrorCancel={this.handleExprotErrorCancel}
                onResponse={() => {
                  const {update} = this.props
                  update({tableLoading: false})
                }}
              >
                {
                  <Button type="primary" disabled={(importModal.costCenterName && importModal.costCenterName.value) && (importModal.requisitionsTypeCode && importModal.requisitionsTypeCode.value) ? false : true} style={{marginRight: '84px', marginLeft: '10px'}}>导入</Button>
                }
              </Import>
            </Col>
          </Row>
        </div>
      </Modal>

    )
  }
}



