import {Bread} from 'Bread'
import React, {PureComponent} from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import CreateModal from './components/./CreateModal'
import RecordModal from './components/./RecordModal'
import FullModal from './components/./FullModal'
import {connect} from 'react-redux'
import {mapDispatchToProps} from './redux/mapDispatchToProps'
import modal from './redux/modal'

@connect((state) => ({...state.reqManagement}), mapDispatchToProps)
export default class App extends PureComponent {
  constructor() {
    super()
  }
  componentWillUnmount() {
    const {update} = this.props
    update(modal)
  }
  render() {
    const {history} = this.props
    return (
      <section>
        <Bread route={['库内作业', '领用管理']} />
        <Form />
        <Tool />
        <Table history={history} />
        {this.props.isShowCreateModal ? <CreateModal /> : null}
        {this.props.isShowRecordModal ? <RecordModal /> : null}
        {this.props.isShowFullModal ? <FullModal /> : null}
      </section>
    )
  }
}



