import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  Input,
  message
} from 'antd'
import {CsxSelect, CsxFormSearch, CsxValidateInput} from 'Form'
import {API_GETREQUISITIONSTTYPEKEYVALUE, API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE, API_STOCKRELOCATION_SHIPPERKEYVALUES, API_NEWPRODUCTSTOCK_GETPRODUCTKEYVALUES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reqManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '操作',
      width: 100,
      key: 'caozuo',
      dataIndex: 'caozuo',
      render: (text, record, index) => <div className="tableOperation">
        {
          record.productCode ? <span className='red' onClick={() => {
            for (let key in record) {
              if (key == 'id') continue
              delete record[key]
            }
            this.forceUpdate()
          }}>清空</span> : null
        }
      </div>
    }, {
      title: '商品编码',
      width: 250,
      dataIndex: 'productCode',
      key: 'productCode',
      render: (text, record, index) => {
        const {userLoation} = this.props
        return (
          <CsxFormSearch
            dateApi={API_NEWPRODUCTSTOCK_GETPRODUCTKEYVALUES}
            searchParams={{warehouseCode: userLoation.value}}
            searchParamsKey='productCode'
            selectItem={{data: record.productCode, key: 'key', valueKey: ['key', 'value']}}
            onChangeSelect={(value) => {
              record.productCode = value
              this.forceUpdate()
            }}
            onSelect={(saveData) => {
              record.productCode = saveData
              this.forceUpdate()
              const {handleGetGoodsInfo, userLoation, update, popData} = this.props
              handleGetGoodsInfo({searchParams: {warehouseCode: userLoation.value, productCode: saveData.value, shipperCode: popData.shipperCode && popData.shipperCode.value}}, (data) => {
                record.productName = data.productName
                record.unit = data.unit
                record.availableQty = data.availableQty
                update({
                  availableQty: data.availableQty
                })
                this.forceUpdate()
                if (record.availableQty === 0) {
                  message.error(`${value.children}的可用量为0`)
                }
              })
            }}
          />
        )
      }
    }, {
      title: '单位',
      width: 150,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '可用量',
      width: 150,
      dataIndex: 'availableQty',
      key: 'availableQty'
    }, {
      title: '领用数量',
      width: 150,
      key: 'requisitionsQty',
      render: (text, record, index) =>
        <CsxValidateInput style={{width: '100px'}} reg={(record.unit && record.unit.toUpperCase()) == 'KG' ? 'TWOO' : 'REGO'} value={record.requisitionsQty ? record.requisitionsQty.value == undefined ? record.requisitionsQty : record.requisitionsQty.value : ''} error={record.requisitionsQty && record.requisitionsQty.error || false} handleOnchange={(data) => {
          record.requisitionsQty = data
          const {update} = this.props
          update({requisitionsQty: record.requisitionsQty, requisitionsQtyStute: record.requisitionsQty.error})
          this.forceUpdate()
        }} />
    }]
  }
  componentDidMount() {

  }
  onInputChange(values) {
    const {popData, update} = this.props
    console.log(values)
    update({popData: {...popData, ...values}})
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowCreateModal: false})
  }
  // 确认
  handleCommit = () => {
    const {
      handleCommit,
      createTable,
      popData,
      requisitionsQty,
      requisitionsQtyStute
    } = this.props
    console.log(popData, 333)
    if (!popData.shipperCode) {
      message.error('请选择货主')
      return
    }
    if (popData.requisitionsTypeCode && !popData.requisitionsTypeCode.value) {
      message.error('请选择领用类型')
      return
    }
    if (!popData.costCenterName || !popData.costCenterName.value) {
      message.error('请选择成本中心')
      return
    }
    if (popData.shipperCode.value) {
      let req = {}
      req.items = JSON.parse(JSON.stringify(createTable)).filter((o, i) => o.productCode)
      if (req.items.length == 0) {
        message.error('请输入商品编码')
        return
      }
      if (requisitionsQty === undefined) {
        message.error('请输入领用数量')
        return
      }
      if (requisitionsQtyStute === true) {
        message.error('领用数量不正确')
        return
      }
      req.items.map((o) => {
        o.productCode = o.productCode && o.productCode.value
        o.requisitionsQty = o.requisitionsQty && o.requisitionsQty.value
        delete o.productName
        delete o.unit
      })
      req.header = {
        shipperName: popData.shipperCode.children,
        shipperCode: popData.shipperCode.value,
        costCenterCode: popData.costCenterName.value,
        costCenterName: popData.costCenterName.children,
        remark: popData.remark,
        requisitionsTypeName: popData.requisitionsTypeCode.children,
        requisitionsTypeCode: popData.requisitionsTypeCode.value
      }
      handleCommit({data: req}, () => {
        message.success('创建成功')
        const {handleGetList, page, size, update} = this.props
        update({isShowCreateModal: false, createTable})
        handleGetList({searchParams: {page, size}})
      })
    }
  }
  render() {
    const {
      userLoation,
      createTableLoading,
      createTable,
      popData: {
        costCenterName,
        requisitionsTypeCode,
        shipperCode,
        remark
      },
      popData,
      update
    } = this.props
    return (
      <Modal
        title="领用单创建"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1000}
      >
        <div className='content'>
          <Row type="flex" justify="start">
            <Col span={12}>
              <CsxSelect
                label='货主'
                searchParams={{warehouseCode: userLoation.value}}
                dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
                selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  this.onInputChange({shipperCode: value})
                }}
              />
            </Col>
            <Col span={12}>
              <CsxSelect
                dateApi={API_GETREQUISITIONSTTYPEKEYVALUE}
                searchParams={{wmsOrderType: '库内领用'}}
                label='领用类型'
                require={true}
                selectItem={{data: requisitionsTypeCode, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  popData.costCenterName = {}
                  update({...popData})
                  this.onInputChange({requisitionsTypeCode: value})
                }}
              />
            </Col>
            <Col span={12}>
              <div className='commonInput'>
                <label>备注：</label>
                <div className='box'>
                  <Input value={remark} onChange={(e) => {
                    this.onInputChange({remark: e.target.value})
                  }} />
                </div>
              </div>
            </Col>
            <Col span={12}>
              <CsxSelect
                dateApi={API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE}
                label='成本中心'
                searchParams={{requisitionsTypeCode: popData.requisitionsTypeCode && popData.requisitionsTypeCode.value}}
                antd={{disabled: popData.requisitionsTypeCode && popData.requisitionsTypeCode.value ? false : true}}
                require={true}
                selectItem={{data: costCenterName, key: 'key', valueKey: ['key', 'value']}}
                onChangeSelect={(value) => {
                  this.onInputChange({costCenterName: value})
                }}
              />
            </Col>
          </Row>
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={record => record.id}
              loading={createTableLoading}
              columns={this.columns}
              scroll={{x: 720, y: 400}}
              dataSource={createTable}
              pagination={false}
            />
            <Col>
              <p style={{color: 'red'}}>注: 称重单位（kg）可输入三位小数，非称重单位只能输入整数</p>
            </Col>
            <Row type="flex" justify="end" style={{marginTop: '15px'}}>
              <Col>
                <Button type="primary" onClick={this.handleCommit} >提交</Button>
              </Col>
            </Row>
          </div>
        </div>
      </Modal>
    )
  }
}



