import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  message,
  DatePicker
} from 'antd'
import style from './style/Form.scss'
import {STATUS} from 'Mock'
import {CsxSelect} from 'Form'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {API_GETREQUISITIONSTTYPEKEYVALUE, API_STOCKRELOCATION_SHIPPERKEYVALUES, API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE} from 'api'
import moment from 'moment'
const {RangePicker} = DatePicker
@connect(
  state => ({...state.reqManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  // 设置默认的时间  前一周 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preWeek = moment().subtract(7, 'd').startOf('day')
    update({searchParams: {initDate: [preWeek, moment().endOf('day')]}})
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }

  componentDidMount() {
    // 获取货主默认的值
    // const {handleGetSupplier, userLoation} = this.props
    // handleGetSupplier({warehouseCode:userLoation.value})
  }

  // 查询数据
  query = () => {
    const {handleGetList, searchParams, userLoation, size} = this.props
    let reqObj = {...searchParams, warehouseCode: userLoation.value}
    // for (let i in reqObj) {
    //   if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
    //     reqObj[i] = reqObj[i].value
    //   }
    // }
    // if (reqObj.initDate && reqObj.initDate.length) {
    //   reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    //   reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
    // }
    // delete reqObj.initDate
    const {initDate = []} = searchParams
    // 判断创建时时间是否为空
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('请选择创建时间后搜索')
    }
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }

  // 新增
  create = () => {
    const {update} = this.props
    let createTable = new Array(50).fill({})
    createTable.map((o, i) => {
      createTable[i] = {id: i}
    })
    update({isShowCreateModal: true, createTable, popData: {}})
  }
  // 重置
  reset = () => {
    const {update, handleGetSupplier, userLoation} = this.props
    update({searchParams: {}})
      // 重置时间
    this.initDateOpt()
    handleGetSupplier({warehouseCode: userLoation.value})
  }
  render() {
    const {
      userLoation,
      searchParams: {
        shipperCode,
        orderCode,
        status,
        costCenterCode,
        initDate,
        requisitionsTypeCode
      }
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <CsxSelect
              label='货主'
              searchParams={{warehouseCode: userLoation.value}}
              dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
              selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shipperCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>领用单号：</label>
              <div className='box'>
                <Input value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              label='状态'
              data={STATUS}
              selectItem={{data: status, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({status: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              searchParams={{wmsOrderType: '库内领用'}}
              dateApi={API_GETREQUISITIONSTTYPEKEYVALUE}
              label='领用类型'
              selectItem={{data: requisitionsTypeCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({requisitionsTypeCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_REQUISITIONSORDERS_GETCOSTCENTERKEYVALUE}
              label='成本中心'
              selectItem={{data: costCenterCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({costCenterCode: value})
              }}
            />
          </Col>
          <Col span={12}>
            <div className='commonInput'>
              <label>创建时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              <Button type="primary" className={style.mr10} onClick={this.create} >创建</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



